* {
  margin: 0;
  padding: 0;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}
body {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: whitesmoke;
  height: 100vh;
}

.header {
  font-size: 1.8rem;
  margin-bottom: 15px;
  color: #f5f5f5;
  text-align: center;
}

.main {
  background-color: rgb(93, 179, 255);
  padding: 20px;
  border-radius: 10px;
}

input {
  font-size: 1.3rem;
  color: #f5f5f5;
  background-color: transparent;
  border: none;
  border-bottom: 3px solid slateblue;
  outline: none;
}
input::placeholder {
  color: #f5f5f5c9;
}

button {
  font-size: 1.3rem;
  color: #f5f5f5;
  background-color: slateblue;
  border: none;
  width: 35px;
  height: 35px;
  text-align: center;
  border-radius: 50px;
  margin-left: 15px;
  cursor: pointer;
}
button:hover {
  background-color: seagreen;
}

li {
  text-align: center;
  list-style-type: none;
  font-size: 1.3rem;
  font-weight: 500;
  color: #f5f5f5;
}
.todo_item {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  /* text-align: center; */
}
.x-mark {
  color: white;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: slateblue;
  font-size: 1rem;
  text-align: center;
  cursor: pointer;
}
.x-mark:hover {
  background-color: salmon;
}
